@use "sass:math";
.section-kv {
    position: relative;
    background: #000 url('../images/kv-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    overflow: hidden;
    aspect-ratio: 16/8;
    @include xs {
        background-image: url('../images/kv-bg-m.jpg');
        aspect-ratio: 3/5.5;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-15%,0);
        z-index: 10;
    }
    &__sub-title-1 {
        width: calc((511 / 1920) * 100vw);
        margin: 0 auto calc((8 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 60vw;
            margin-bottom: 2vw;
        }
    }
    &__main-title {
        width: calc((384 / 1920) * 100vw);
        margin: 0 auto calc((14 / 1920) * 100vw);
        filter: drop-shadow(0 1rem 2rem #000);
        @include xs {
            width: 48vw;
            margin-bottom: 2vw;
        }
        img {
            @extend %rsp-img;
            mix-blend-mode: lighten;
        }
    }
    &__sub-title-2 {
        width: calc((487 / 1920) * 100vw);
        margin: 0 auto calc((10 / 1920) * 100vw);
        @include xs {
            width: 74vw;
            margin-bottom: 2vw;
        }
        img {
            @extend %rsp-img;
        }
    }
    &__sub-title-3 {
        width: calc((597 / 1920) * 100vw);
        margin: 0 auto;
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 82vw;
        }
    }
    &__video {
        width: calc((603 / 1920) * 100vw);
        height: calc((108 / 1920) * 100vw);
        background: transparent url('../images/play_btn@2x.png') no-repeat center bottom;
        background-size: auto 100%;
        display: flex;
        margin: 0 auto;
        @include xs {
            width: calc((603 / 414) * 53vw);
            height: calc((108 / 414) * 53vw);
        }
        a {
            flex: 1;
            height: 100%;
            display: block;
            font-size: 0;
        }
    }
    &__btn-reserve {
        @extend %site-btn;
        width: calc((435 / 1920) * 100vw);
        @include xs {
            width: 60vw;
        }
    }
    &__nav {
        display: flex;
        width: 100%;
        justify-content: center;
        gap: calc((12 / 1920) * 100vw);
    }
}