.fail {
    &__main-title {
        width: calc((659 / 1920) * 100vw);
        margin: 0 auto calc((30 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 95vw;
            margin-bottom: 2vw;
        }
    }
    &__box {
        width: calc((804 / 1920) * 100vw);
        height: calc((407 / 1920) * 100vw);
        background: transparent url('../images/member-fail-box-bg@2x.png') no-repeat center top;
        background-size: cover;
        position: relative;
        box-sizing: border-box;
        padding-left: calc((40 / 1920) * 100vw);
        padding: calc((40 / 1920) * 100vw);
        margin: 0 auto;
        z-index: 10;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: calc((20 / 1920) * 100vw);
        @include xs {
            width: calc((804 / 414) * 48vw);
            height: calc((407 / 414) * 48vw);
            padding-left: calc((20 / 414) * 100vw);
            padding-right: calc((20 / 414) * 100vw);
            gap: 4vw;
        }
    }
    &__text {
        font-size: calc((20 / 1920) * 100vw);
        color: #fff;
        text-align: center;
        line-height: 1.6;
        @include xs {
            font-size: 2.6vw;
        }
    }
    &__btn-back {
        display: block;
        width: calc((358 / 1920) * 100vw);
        border: 0;
        background-color: transparent;
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 45vw;
        }
    }
}