.popup {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 150;
    left: 0;
    top: 0;
    opacity: 0;
    pointer-events: none;
    transition: opacity 500ms ease;
    &.active {
        pointer-events: auto;
        opacity: 1;
        z-index: 160;
    }
    &.popup-form {
        z-index: 155 !important;
    }
    &__overlay {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.85);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__content {
        position: relative;
    }
    &__video-container {
        width: calc((1081 / 1920) * 100vw);
        aspect-ratio: 16 / 9.35;
        position: relative;
        @include xs {
            width: 90vw;
        }
        iframe {
            width: calc(100% - 0.5vw);
            height: calc(100% - 0.5vw);
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            z-index: 5;
            background-color: #000;
        }
    }
    &__frame {
        background: transparent url('../images/role-frame@2x.png') no-repeat 0 0;
        background-size: cover;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 10;
        pointer-events: none;
    }
    &__btn-close {
        width: calc((40 / 1920) * 100vw);
        height: calc((40 / 1920) * 100vw);
        background: transparent url('../images/role-btn-close@2x.png') no-repeat 0 0;
        background-size: cover;
        right: calc((44 / 1920) * -100vw);
        top: 0;
        position: absolute;
        border: 0;
        padding: 0;
        font-size: 0;
        @include xs {
            width: 7vw;
            height: 7vw;
            right: inherit;
            left: 50%;
            transform: translate3d(-50%,0,0);
            top: -8vw;
        }
    }
}