.float-nav {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate3d(260%,-50%,0);
    z-index: 150;
    display: flex;
    flex-direction: column;
    gap: calc((20 / 1920) * 100vw);
    @include xs {
        display: none;
    }
    &__btn-fb,
    &__btn-yt {
        width: calc((253 / 1920) * 100vw);
        height: calc((191 / 1920) * 100vw);
        position: relative;
        z-index: 0;
        transition: transform 300ms ease;
        &:hover {
            transform: translate3d(0,-0.8rem,0);
        }
        a {
            width: 100%;
            height: 100%;
            z-index: 0;
            display: block;
            position: relative;
            font-size: 0;
        }
    }
    &__btn-fb {
        background: transparent url('../images/float-btn-fb@2x.png') no-repeat 0 0;
        background-size: cover;
    }
    &__btn-yt {
        background: transparent url('../images/float-btn-yt@2x.png') no-repeat 0 0;
        background-size: cover;
    }
    &__btn-close {
        font-size: 0;
        width: calc((28 / 1920) * 100vw);
        height: calc((27 / 1920) * 100vw);
        background: transparent url('../images/float-btn-close@3x.png') no-repeat 0 0;
        background-size: cover;
        position: absolute;
        right: calc((60 / 1920) * 100vw);
        top: calc((10 / 1920) * 100vw);
        border: 0;
        z-index: 10;
    }
}