.section-reserve {
    position: relative;
    background: #000 url('../images/reserve-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    aspect-ratio: 16/8;
    overflow: hidden;
    @include xs {
        background-image: url('../images/reserve-bg-m.jpg');
        aspect-ratio: 3/5.5;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        z-index: 10;
        @include xs {
            transform: translate3d(-50%,0,0);
            top: 16vw;
        }
    }
    &__sub-title-1 {
        width: calc((388 / 1920) * 100vw);
        margin: 0 auto calc((12 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 50vw;
            margin-bottom: 2vw;
        }
    }
    &__main-title {
        width: calc((745 / 1920) * 100vw);
        margin: 0 auto calc((12 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 95vw;
            margin-bottom: 2vw;
        }
    }
    &__sub-title-2 {
        width: calc((534 / 1920) * 100vw);
        margin: 0 auto calc((24 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 70vw;
            margin-bottom: 50vw;
        }
    }
    &__role {
        position: absolute;
        z-index: 0;
        left: 50%;
        bottom: 0;
        transform: translate3d(-110%,0,0);
        width: calc((1119 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
    }
    &__box {
        width: calc((804 / 1920) * 100vw);
        height: calc((407 / 1920) * 100vw);
        background: transparent url('../images/reserve-box-bg@2x.png') no-repeat center top;
        background-size: cover;
        position: relative;
        box-sizing: border-box;
        padding-top: calc((40 / 1920) * 100vw);
        margin: 0 auto calc((20 / 1920) * 100vw);
        @include xs {
            background-image: url('../images/reserve-box-bg-m@2x.png');
            width: calc((804 / 414) * 48vw);
            height: calc((520 / 414) * 48vw);
            padding-top: 6vw;
            margin-bottom: 4vw;
        }
    }
    &__item {
        width: calc((260 / 1920) * 100vw);
        position: absolute;
        right: 0;
        bottom: 0;
        transform: translate3d(35%, 23%, 0);
        pointer-events: none;
        img {
            @extend %rsp-img;
            pointer-events: none;
        }
        @include xs {
            width: 30vw;
            transform: translate3d(13vw,20%,0);
        }
    }
    &__remind-text {
        font-size: calc((15 / 1920) * 100vw);
        color: #fff150;
        line-height: 1.6;
        position: absolute;
        left: calc((40 / 1920) * 100vw);
        bottom: calc((40 / 1920) * 100vw);
        @include xs {
            font-size: 2.6vw;
            left: calc((60 / 414) * 48vw);
            bottom: calc((40 / 414) * 48vw);
        }
        a {
            color: #fff150;
            border-bottom: 0.1rem solid #fff150;
        }
    }
    &__nav {
        display: flex;
        width: 100%;
        justify-content: center;
        position: relative;
        z-index: 10;
        gap: calc((12 / 1920) * 100vw);
        @include xs {
            gap: 1.5vw;
        }
    }
    &__btn-submit,
    &__btn-event {
        width: calc((358 / 1920) * 100vw);
        @extend %site-btn;
        @include xs {
            width: 48vw;
        }
    }
    &__item-title {
        width: calc((358 / 1920) * 100vw);
        margin: 0 auto calc((8 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 56vw;
            margin-bottom: 0;
        }
    }
    &__form {
        width: calc((500 / 1920) * 100vw);
        margin: 0 auto;
        @include xs {
            width: 80vw;
        }
    }
    &__row {
        display: flex;
        gap: calc((15 / 1920) * 100vw);
        &:not(:last-child) {
            margin-bottom: calc((15 / 1920) * 100vw);
            @include xs {
                margin-bottom: 2vw;
            }
        }
        @include xs {
            gap: 2vw;
        }
    }
    &__select-wrap {
        @extend %site-select;
        width: calc((200 / 1920) * 100vw);
        @include xs {
            width: 40vw;
        }
    }
    &__input-mobile {
        border-radius: 0;
        flex: 1;
        font-size: calc((20 / 1920) * 100vw);
        color: #e6c899;
        border: 0;
        background-color: #352c25;
        text-align: center;
        &::placeholder {
            color: #e6c899;
        }
        @include xs {
            font-size: 4vw;
            width: 50vw;
        }
    }
    &__checkbox {
        @extend %site-checkbox;
    }
    &__policies {
        box-sizing: border-box;
        padding: calc((8 / 1920) * 100vw);
        width: calc((320 / 1920) * 100vw);
        height: calc((130 / 1920) * 100vw);
        overflow-y: auto;
        background-color: #352c25;
        scrollbar-width: auto;
        scrollbar-color: #e6c899 #393939;
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        &::-webkit-scrollbar {
            width: 4px;
        }
        &::-webkit-scrollbar-track {
            background: #393939;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #e6c899;
            border-radius: 10px;
        }
        @include xs {
            width: 60vw;
            height: 30vw;
            padding: 1.5vw;
        }
        h3 {
            color: #e6c899;
            font-size: calc((12 / 1920) * 100vw);
            line-height: 1.6;
            @include xs {
                font-size: 3vw;
            }
        }
        p {
            line-height: 1.6;
            color: #e6c899;
            font-size: calc((12 / 1920) * 100vw);
            @include xs {
                font-size: 3vw;
            }
        }
        ul {
            li {
                line-height: 1.6;
                color: #e6c899;
                font-size: calc((10 / 1920) * 100vw);
                @include xs {
                    font-size: 3vw;
                }
            }
        }
    }
}