.site-header {
    background-color: #070707;
    height: calc((74 / 1920) * 100vw);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    @include xs {
        height: calc((74 / 414) * 100vw);
        background-color: rgba(0,0,0,0.7);
    }
    &__inner {
        width: calc((1800 / 1920) * 100vw);
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        @include xs {
            width: 100%;
            padding-left: 2.4rem;
            padding-right: 2.4rem;
            justify-content: flex-end;
        }
        @include sm {
            width: 100%;
            padding-left: 2.4rem;
            padding-right: 2.4rem;
        }
    }
    &__btn-close {
        width: 9vw;
        height: 9vw;
        background-color: transparent;
        z-index: 15;
        position: absolute;
        border: 0;
        top: 5.2vw;
        font-size: 0;
        right: 8vw;
        display: none;
        &::before,
        &::after {
            width: 100%;
            height: 0.2rem;
            background-color: #fff;
            position: absolute;
            content: "";
            left: 50%;
            top: 50%;
        }
        &::before {
            transform: translate3d(-50%,-50%,0) rotate(-45deg);
        }
        &::after {
            transform: translate3d(-50%,-50%,0) rotate(45deg);
        }
        @include xs {
            display: block;
            opacity: 0;
            transition: opacity 500ms ease;
            &.active {
                opacity: 1;
            }
        }
    }
    &__btn-open {
        width: 8vw;
        height: 6vw;
        background-color: transparent;
        z-index: 5;
        position: absolute;
        border: 0;
        top: 5.6vw;
        font-size: 0;
        left: 8vw;
        display: none;
        &::before,
        &::after {
            width: 100%;
            height: 0.2rem;
            background-color: #fff;
            position: absolute;
            content: "";
            left: 50%;
        }
        &::before {
            transform: translate3d(-50%,0,0);
            top: 0;
        }
        &::after {
            transform: translate3d(-50%,0,0);
            bottom: 0;
        }
        span {
            width: 100%;
            height: 0.2rem;
            background-color: #fff;
            position: absolute;
            top: 50%;
            transform: translate3d(0,-50%,0);
            left: 0;
        }
        @include xs {
            display: block;
        }
        @include sm {
            display: block;
        }
    }
    &__odin {
        width: calc((84 / 1920) * 100vw);
        height: calc((38 / 1920) * 100vw);
        background: transparent url('../images/odin-logo@3x.png') no-repeat 0 0;
        background-size: cover;
        @include xs {
            width: calc((84 / 414) * 100vw);
            height: calc((38 / 414) * 100vw);
            position: absolute;
            left: 50%;
            top: 15vw;
            transform: translate3d(-50%,0,0);
            z-index: 15;
            opacity: 0;
            transition: opacity 500ms ease;
            &.active {
                opacity: 1;
            }
        }
    }
    &__nav {
        display: flex;
        margin-left: auto;
        margin-right: calc((200 / 1920) * 100vw);
        height: 100%;
        @include xs {
            box-sizing: border-box;
            position: fixed;
            flex-direction: column;
            width: 100%;
            height: 100vh;
            padding-top: 40vw;
            background-color: #000;
            top: 0;
            left: 0;
            margin-right: 0;
            transform: translate3d(0,-200%,0);
            z-index: 10;
            transition: transform 500ms ease 100ms;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        @include sm {
            margin-right: 2vw;
        }
    }
    &__btn {
        font-family: 'Noto Sans TC', sans-serif;
        color: #fff;
        display: flex;
        align-items: center;
        padding: 0 calc((62 / 1920) * 100vw);
        font-size: calc((16 / 1920) * 100vw);
        transition: background-color 300ms ease;
        height: 100%;
        &:hover {
            background-color: #5e4518;
        }
        &.active {
            background-color: #5e4518;
        }
        @include xs {
            height: 8rem;
            font-size: 4vw;
            justify-content: center;
        }
        @include sm {
            padding-left: 2vw;
            padding-right: 2vw;
        }
    }
    &__link {
        display: flex;
        align-items: center;
    }
    &__btn-link {
        font-size: calc((14 / 1920) * 100vw);
        color: #7e7e7e;
        padding: 0 calc((18 / 1920) * 100vw);
        position: relative;
        transition: color 300ms ease;
        @include xs {
            font-size: 3.4vw;
            padding-left: 2vw;
            padding-right: 2vw;
        }
        &:hover {
            color: #fff;
        }
        &:nth-child(1) {
            &::after {
                width: calc((2 / 1920) * 100vw);
                background-color: #7e7e7e;
                height: calc((12 / 1920) * 100vw);
                position: absolute;
                right: calc((1 / 1920) * -100vw);
                top: 50%;
                transform: translate3d(0,-50%,0);
                content: "";
                position: absolute;
                @include xs {
                    width: 0.1rem;
                    height: 2vw;
                }
            }
        }
    }
}