.privacy {
    &__main-title {
        width: calc((940 / 1920) * 100vw);
        margin: 0 auto calc((30 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 95vw;
            margin-bottom: 2vw;
        }
    }
    &__box {
        width: calc((804 / 1920) * 100vw);
        height: calc((407 / 1920) * 100vw);
        background: transparent url('../images/privacy-box-bg@2x.png') no-repeat center top;
        background-size: cover;
        position: relative;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding-left: calc((40 / 1920) * 100vw);
        padding-right: calc((100 / 1920) * 100vw);
        margin: 0 auto;
        z-index: 10;
        @include xs {
            width: calc((804 / 414) * 48vw);
            height: calc((407 / 414) * 48vw);
            padding-left: calc((20 / 414) * 100vw);
            padding-right: calc((40 / 414) * 100vw);
        }
    }
    &__btn-close {
        width: calc((35 / 1920) * 100vw);
        height: calc((35 / 1920) * 100vw);
        background: transparent url('../images/privacy-btn-close@2x.png') no-repeat 0 0;
        background-size: cover;
        right: calc((35 / 1920) * 100vw);
        top: calc((35 / 1920) * 100vw);
        position: absolute;
        border: 0;
        padding: 0;
        font-size: 0;
        @include xs {
            width: 5vw;
            height: 5vw;
            right: inherit;
            right: calc((35 / 414) * 48vw);
            top: calc((35 / 414) * 48vw);
        }
    }
    &__ul-list {
        li {
            font-size: calc((20 / 1920) * 100vw);
            color: #fff;
            line-height: 1.6;
            padding-left: calc((20 / 1920) * 100vw);
            position: relative;
            &:not(:last-child) {
                margin-bottom: calc((36 / 1920) * 100vw);
            }
            &::before {
                content: "*";
                font-size: calc((20 / 1920) * 100vw);
                color: #fff;
                line-height: 1.6;
                position: absolute;
                left: 0;
                top: 0;
                transform: translate3d(0,10%,0);
                @include xs {
                    font-size: 2.6vw;
                    top: 0.5vw;
                }
            }
            @include xs {
                font-size: 2.6vw;
                padding-left: calc((20 / 414) * 48vw);
            }
        }
    }
}