.section-update {
    position: relative;
    background: #000 url('../images/update-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    aspect-ratio: 16/8;
    overflow: hidden;
    @include xs {
        background-image: url('../images/update-bg-m.jpg');
        aspect-ratio: 3/4;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        z-index: 10;
    }
    &__main-title {
        width: calc((663 / 1920) * 100vw);
        margin: 0 auto;
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 96vw;
        }
    }
    .swiper {
        width: calc((1200 / 1920) * 100vw);
        position: relative;
        .swiper-slide {
            filter: brightness(0.4);
        }
        .swiper-slide-active {
            filter: brightness(1);
        }
        @include xs {
            width: 96vw;
        }
    }
    &__slide-content {
        width: calc((832 / 1920) * 100vw);
        height: calc((485 / 1920) * 100vw);
        margin: 0 auto;
        overflow: hidden;
        position: relative;
        img {
            @extend %rsp-img;
            position: relative;
            z-index: 0;
        }
        @include xs {
            width: calc((832 / 414) * 40vw);
            height: calc((485 / 414) * 40vw);
        }
    }
    &__intro {
        width: 100%;
        position: absolute;
        z-index: 10;
        bottom: calc((20 / 1920) * 100vw);
        left: 50%;
        transform: translate3d(-50%,0,0);
        @include xs {
            bottom: 2vw;
        }
        h3 {
            font-size: calc((26 / 1920) * 100vw);
            line-height: 1.6;
            color: #fff;
            text-align: center;
            @include xs {
                font-size: 4vw;
            }
        }
        p {
            font-size: calc((22 / 1920) * 100vw);
            line-height: 1.6;
            color: #fff;
            text-align: center;
            @include xs {
                font-size: 3vw;
            }
        }
        small {
            padding-top: calc((8 / 1920) * 100vw);
            font-size: calc((16 / 1920) * 100vw);
            color: #b7ad6e;
            text-align: center;
            padding-left: calc((20 / 1920) * 100vw);
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: calc((8 / 1920) * 100vw);
            @include xs {
                font-size: 2.5vw;
            }
            img {
                width: calc((16 / 1920) * 100vw);
                height: calc((16 / 1920) * 100vw);
                @include xs {
                    width: 2.6vw;
                    height: 2.6vw;
                }
            }
        }
    }
    .cus-swiper-button-prev {
        width: calc((63 / 1920) * 100vw);
        height: calc((85 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-800%,50%,0);
        z-index: 30;
        background: transparent url('../images/update-btn-prev@2x.png') no-repeat 0 0;
        background-size: cover;
        cursor: pointer;
        @include xs {
            width: calc((63 / 414) * 50vw);
            height: calc((85 / 414) * 50vw);
            transform: translate3d(-50%,-180%,0) rotate(90deg);
        }
    }
    .cus-swiper-button-next {
        width: calc((63 / 1920) * 100vw);
        height: calc((85 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(700%,50%,0);
        z-index: 30;
        background: transparent url('../images/update-btn-next@2x.png') no-repeat 0 0;
        background-size: cover;
        cursor: pointer;
        @include xs {
            width: calc((63 / 414) * 50vw);
            height: calc((85 / 414) * 50vw);
            transform: translate3d(-50%,420%,0) rotate(90deg);
        }
    }
    .cus-swiper-button-disabled {
        opacity: 0.3;
        pointer-events: none;
    }
    .cus-swiper-pagination {
        display: flex;
        justify-content: center;
        padding: calc((48 / 1920) * 100vw) 0;
        gap: 0;
        @include xs {
            padding-top: 4vw;
            padding-bottom: 18vw;
        }
    }
    .cut-swiper-pagination-bullet {
        padding: 0 calc((35 / 1920) * 100vw);
        font-size: calc((25 / 1920) * 100vw);
        color: #fff;
        background-size: calc((355 / 1920) * 100vw) auto;
        position: relative;
        cursor: pointer;
        @include xs {
            font-size: 2.4vw;
            padding-left: 1vw;
            padding-right: 1vw;
        }
        &::before {
            bottom: 0;
            left: 50%;
            transform: translate3d(-50%,120%,0);
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: transparent url('../images/update-pagination@2x.png') no-repeat center bottom;
            background-size: calc((355 / 1920) * 100vw) auto;
            @include xs {
                background-size: calc((355 / 414) * 40vw) auto;
                transform: translate3d(-50%,150%,0);
            }
        }
        &::after {
            bottom: 0;
            left: 50%;
            transform: translate3d(-50%,54%,0);
            position: absolute;
            content: "";
            width: calc((363 / 1920) * 100vw);
            height: calc((348 / 1920) * 100vw);
            background: transparent url('../images/update-pagination-active@2x.png') no-repeat center bottom;
            background-size: calc((363 / 1920) * 100vw) auto;
            transition: opacity 300ms ease;
            opacity: 0;
            @include xs {
                width: calc((363 / 414) * 50vw);
                height: calc((348 / 414) * 50vw);
                transform: translate3d(-50%,27%,0);
            }
        }
    }
    .cus-swiper-pagination-bullet-active {
        &::after {
            opacity: 1;
        }
    }
}