.section-invite {
    position: relative;
    background: #000 url('../images/invite-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    aspect-ratio: 16/8;
    overflow: hidden;
    @include xs {
        background-image: url('../images/invite-bg-m.jpg');
        aspect-ratio: 3/6;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        z-index: 10;
        @include xs {
            transform: translate3d(-50%,0,0);
            top: 16vw;
        }
    }
    &__sub-title-1 {
        width: calc((281 / 1920) * 100vw);
        margin: 0 auto calc((12 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 50vw;
            margin-bottom: 2vw;
        }
    }
    &__sub-title-2 {
        width: calc((534 / 1920) * 100vw);
        margin: 0 auto calc((24 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 75vw;
            margin-bottom: 50vw;
        }
    }
    &__main-title {
        width: calc((659 / 1920) * 100vw);
        margin: 0 auto calc((12 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 95vw;
            margin-bottom: 2vw;
        }
    }
    &__box {
        width: calc((804 / 1920) * 100vw);
        height: calc((407 / 1920) * 100vw);
        background: transparent url('../images/invite-box-bg@2x.png') no-repeat center top;
        background-size: cover;
        position: relative;
        box-sizing: border-box;
        padding-top: calc((24 / 1920) * 100vw);
        padding-left: calc((40 / 1920) * 100vw);
        padding-right: calc((40 / 1920) * 100vw);
        margin: 0 auto calc((40 / 1920) * 100vw);
        &.friends-return {
            padding-top: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: calc((20 / 1920) * 100vw);
            @include xs {
                background-image: url('../images/invite-box-bg@2x.png');
                width: calc((804 / 414) * 48vw);
                height: calc((407 / 414) * 48vw);
                padding-top: 4vw;
                margin-bottom: 4vw;
                padding-top: 0;
            }
        }
        @include xs {
            background-image: url('../images/invite-box-bg-m@2x.png');
            width: calc((804 / 414) * 48vw);
            height: calc((635 / 414) * 48vw);
            padding-top: 4vw;
            margin-bottom: 4vw;
        }
    }
    &__intro {
        width: 100%;
        margin: 0 auto;
        @include xs {
            width: 80vw;
        }
        h3 {
            font-size: calc((30 / 1920) * 100vw);
            color: #eece75;
            text-align: center;
            line-height: 1.6;
            @include xs {
                font-size: 4vw;
            }
        }
        p {
            font-size: calc((20 / 1920) * 100vw);
            color: #fff;
            text-align: center;
            line-height: 1.6;
            margin-bottom: 0.4rem;
            @include xs {
                font-size: 2.6vw;
                margin-bottom: 0.5vw;
            }
        }
        ul {
            counter-reset: invite-list;
            margin-bottom: calc((12 / 1920) * 100vw);
            @include xs {
                width: 75vw;
                margin: 0 auto 2vw;
            }
            li {
                counter-increment: invite-list;
                font-size: calc((20 / 1920) * 100vw);
                color: #fff;
                line-height: 1.6;
                padding-left: calc((30 / 1920) * 100vw);
                position: relative;
                a {
                    color: #fff;
                    border-bottom: 0.1rem solid #fff;
                }
                @include xs {
                    font-size: 2.6vw;
                    padding-left: 4vw;
                }
                &:not(:last-child) {
                    margin-bottom: calc((4 / 1920) * 100vw);
                    @include xs {
                        margin-bottom: 0.5vw;
                    }
                }
                &::before {
                    content: counter(invite-list);
                    font-size: calc((14 / 1920) * 100vw);
                    width: calc((24 / 1920) * 100vw);
                    height: calc((24 / 1920) * 100vw);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    left: 0;
                    top: calc((4 / 1920) * 100vw);
                    background-color: #5c4a1c;
                    border-radius: 50%;
                    @include xs {
                        width: 2.6vw;
                        height: 2.6vw;
                        font-size: 2vw;
                        top: 0.8vw;
                    }
                }
            }
        }
    }
    &__form {
        width: calc((700 / 1920) * 100vw);
        margin: 0 auto;
        @include xs {
            width: 80vw;
        }
    }
    &__row {
        display: flex;
        gap: calc((15 / 1920) * 100vw);
        &:not(:last-child) {
            margin-bottom: calc((15 / 1920) * 100vw);
            @include xs {
                margin-bottom: 1.5vw;
                gap: 1.5vw;
            }
        }
        @include xs {
            flex-wrap: wrap;
            gap: 1.5vw;
            width: 100%;
        }
        &.background {
            justify-content: center;
            gap: 0;
            @include xs {
                background-color: #352c25;
            }
        }
        &.section-invite-result {
            display: none;
        }
    }
    &__checkbox {
        @extend %site-checkbox;
        margin: 0;
        width: auto;
        padding: 0 calc((30 / 1920) * 100vw);
    }
    &__select-wrap {
        @extend %site-select;
        width: calc((200 / 1920) * 100vw);
        @include xs {
            width: 30vw;
        }
    }
    &__input-mobile,
    &__input-id {
        background-color: #352c25;
        box-sizing: border-box;
        flex: 1;
        font-size: calc((20 / 1920) * 100vw);
        color: #e6c899;
        border: 0;
        text-align: center;
        padding: 0;
        &::placeholder {
            color: #e6c899;
        }
        @include xs {
            font-size: 4vw;
            border-radius: 0;
            height: 9vw;
        }
    }
    &__input-mobile {
        @include xs {
            flex: 0;
            width: 48.5vw;
        }
    }
    &__input-id {
        @include xs {
            width: 100%;
        }
    }
    &__invite-code,
    &__status {
        width: calc((515 / 1920) * 100vw);
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin: 0 auto;
        gap: calc((10 / 1920) * 100vw);
        @include xs {
            width: 70vw;
            gap: 1.5vw;
        }
        p {
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            flex: 1;
            height: calc((50 / 1920) * 100vw);
            background-color: #352c25;
            font-size: calc((20 / 1920) * 100vw);
            color: #e6c899;
            strong {
                padding: 0 calc((12 / 1920) * 100vw);
                color: #eece75;
            }
            @include xs {
                font-size: 4vw;
                height: calc((50 / 414) * 100vw);
            }
        }
    }
    &__btn-copy {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        width: calc((50 / 1920) * 100vw);
        height: calc((50 / 1920) * 100vw);
        border: 0;
        background: #352c25 url('../images/icon-copy@2x.png') no-repeat center center;
        background-size: calc((29 / 1920) * 100vw);
        @include xs {
            width: calc((50 / 414) * 100vw);
            height: calc((50 / 414) * 100vw);
            background-size: calc((29 / 414) * 48vw);
        }
    }
    &__nav {
        display: flex;
        width: 100%;
        justify-content: space-between;
        gap: calc((12 / 1920) * 100vw);
        &.center {
            justify-content: center;
        }
        @include xs {
            flex-wrap: wrap;
            gap: 1.5rem;
            justify-content: center;
        }
    }
    &__btn-code {
        @extend %site-btn;
        width: calc((358 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 44vw;
        }
    }
    &__btn-submit,
    &__btn-confirm {
        @extend %site-btn;
        width: calc((358 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 45vw;
        }
    }
    &__btn-result {
        @extend %site-btn;
        width: calc((397 / 1920) * 100vw);
        @include xs {
            width: 47vw;
        }
    }
    &__btn-notice {
        @extend %site-btn;
        width: calc((509 / 1920) * 100vw);
        @include xs {
            width: 60vw;
        }
    }
    &__result {
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1;
        height: calc((50 / 1920) * 100vw);
        background-color: #352c25;
        font-size: calc((20 / 1920) * 100vw);
        color: #e6c899;
        strong {
            padding: 0 calc((12 / 1920) * 100vw);
            color: #eece75;
        }
        @include xs {
            font-size: 3vw;
            height: calc((40 / 414) * 100vw);
        }
    }
}