.section-role {
    position: relative;
    background: #000 url('../images/role-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    aspect-ratio: 16/8;
    overflow: hidden;
    @include xs {
        background-image: url('../images/role-bg-m.jpg');
        aspect-ratio: 3/5.5;
    }
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-200%,0);
        z-index: 40;
        @include xs {
            transform: translate3d(-50%,-66vw,0);
        }
    }
    &__sub-title-1 {
        width: calc((545 / 1920) * 100vw);
        margin: 0 auto calc((12 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 62vw;
            margin-bottom: 2vw;
        }
    }
    &__main-title {
        width: calc((883 / 1920) * 100vw);
        margin: 0 auto calc((12 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 100vw;
            margin-bottom: 2vw;
        }
    }
    &__sub-title-2 {
        width: calc((678 / 1920) * 100vw);
        margin: 0 auto calc((24 / 1920) * 100vw);
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 75vw;
            margin-bottom: 2vw;
        }
    }
    &__role-1 {
        position: absolute;
        width: calc((1225 / 1920) * 100vw);
        left: 50%;
        bottom: 0;
        transform: translate3d(-88%, 9%, 0);
        z-index: 10;
        pointer-events: none;
        img {
            @extend %rsp-img;
        }
        @include xs {
            display: none;
        }
    }
    &__role-2 {
        position: absolute;
        width: calc((1521 / 1920) * 100vw);
        left: 50%;
        bottom: 0;
        transform: translate3d(-46%,12%,0);
        z-index: 20;
        pointer-events: none;
        img {
            @extend %rsp-img;
            pointer-events: none;
        }
        @include xs {
            display: none;
        }
    }
    &__intro-1 {
        width: calc((328 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-190%, -36%, 0);
        z-index: 5;
        transition: transform 300ms ease;
        cursor: pointer;
        &:hover {
            transform: translate3d(-190%, -40%, 0);
        }
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 40vw;
            transform: translate3d(-105%, 65%, 0);
            &:hover {
                transform: translate3d(-105%, 65%, 0);
            }
        }
    }
    &__intro-2 {
        width: calc((328 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(90%,-36%,0);
        z-index: 5;
        transition: transform 300ms ease;
        cursor: pointer;
        &:hover {
            transform: translate3d(90%, -40%, 0);
        }
        img {
            @extend %rsp-img;
        }
        @include xs {
            width: 40vw;
            transform: translate3d(5%, 65%, 0);
            &:hover {
                transform: translate3d(5%, 65%, 0);
            }
        }
    }
    &__light-1 {
        width: calc((582 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-40%,0);
        z-index: 30;
        mix-blend-mode: lighten;
        img {
            @extend %rsp-img;
            pointer-events: none;
        }
        @include xs {
            display: none;
        }
    }
    &__light-2 {
        width: calc((582 / 1920) * 100vw);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-40%,-40%,0);
        z-index: 30;
        mix-blend-mode: color-dodge;
        img {
            @extend %rsp-img;
            pointer-events: none;
        }
        @include xs {
            display: none;
        }
    }
}